import "core-js/modules/es.array.push.js";
import { useHead } from '@vueuse/head';
export default {
  name: 'WhiteListOn',
  setup() {
    useHead({
      title: 'Whitelist Mode Active',
      meta: [{
        name: 'robots',
        content: 'noindex, nofollow'
      }, {
        name: 'viewport',
        content: 'width=device-width, initial-scale=1'
      }, {
        charset: 'UTF-8'
      }]
    });
  },
  methods: {
    goHome() {
      this.$router.push('/');
    },
    goBack() {
      if (window.history.length > 1) {
        this.$router.go(-1);
      } else {
        this.$router.push('/');
      }
    }
  }
};